export default{
	data(){
		return{
			num: false,
			isWork: false,
			list: [],
			imgList: [], //预览图片
			formData:{
				page: 1,
				rows: 10
			},
			loading: 0,
			total: 0
		}
	},
	onLoad() {
		//this.refresh(false, true)
	},
	// 页面触底加载更多
	onReachBottom() {
		this.loadmore()
	},
	// 下拉刷新
	onPullDownRefresh() {
		this.refresh(true)
	},
	methods:{
		comb(num){
			this.num = !this.num
		},

		// 刷新数据，fresh是否下拉刷新，onload是否初始化 
		async refresh(fresh = false, onload = false) {
			this.loading = 0
			this.formData.page = 1
			this.list = []
			this.imgList = []
			await this._WorkList()
			if (fresh) {
				uni.stopPullDownRefresh();
				this.ShowToast('刷新成功',2500,false,'success')
			}
		},
		// 打卡列表
		async _WorkList(){
			if (this.loading == 2 || this.loading == 1) {
				return
			}
			this.loading = 1
			const list = await this.$http.WorkList(this.formData)
			this.list = [...this.list,...list.data.result.list]
			this.imgList = this.list.map(item=>{
				return item.pictures
			})
			if (this.formData.page == 1) {
				this.total = list.data.result.total
			}
			this.loading = 0
			if (this.list.length == this.total) {
				this.loading = 2
				return
			}
			this.formData.page++
		},
		loadmore(){
			this._WorkList()
		},
		nowork() {
			this.isWork = !this.isWork
		},
		gobuy(){
			this.isWork = !this.isWork
			uni.navigateTo({
				url: '/pages/goods/goods_detail?id=700'
			})
		},
		previewImage(e) {
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.imgList
			})
		},
		releaseBtn(){
			if(!this.fans){
				this.ShowToast('请先登录',1500,false,'none')
				setTimeout(()=>{
					uni.navigateTo({
						url: '/pages/register/login_index'
					})
				},1500)
				return
			}
			if(this.fans.punch>0){
				uni.navigateTo({
					url: '/pages/sign/puchclock_release'
				})
			}else{
				this.nowork()
			}
		}
	},
	onNavigationBarButtonTap(e) {
			uni.navigateTo({
				url: '/pages/punchclock/puchclock_release'
			});
	}
}
